import { RoutineNodeSize } from "../config/config";

/**
 * type: 0 // 
 * nodeType 0  -1 
 */
class ProcessNode {
  constructor(nodeType, text, { nodeRender }) {
    this.nodeType = nodeType;
    this.text = text;
    this.width = 16;
    this.height = 16;
    this.fontSize = 14;
    this.bgColor = "#E6E6E6";
    this.type = 0;
    this.nodeRender = nodeRender;
    this.reference = [];
    this.pos = 0;
    this.parentNode = null;
    this.parentNode = null;
    this.parentNodeIndex = -1;
  }
  setReference(reference, pos) {
    this.reference = reference;
    this.pos = pos;
  }
  setParentNode(parentNode, parentNodeIndex) {
    this.parentNode = parentNode;
    this.parentNodeIndex = parentNodeIndex;
  }
}

/**
 *  type 2
 * nodeType -2 
 */
class AddConditionNode {
  constructor({ nodeRender, removeNodeMethod }) {
    this.type = 2;
    this.nodeType = -2;
    this.width = 72;
    this.height = 24;
    this.title = "";
    this.reference = [];
    this.currentProcess = [];
    this.pos = 0;
    this.parentNode = null;
    this.parentNodeIndex = -1;
    // 
    this.nodeRender = nodeRender;
    // 
    this.removeNode = removeNodeMethod;
  }
  setReference(reference, pos) {
    this.reference = reference;
    this.pos = pos;
  }
  setParentNode(parentNode, parentNodeIndex) {
    this.parentNode = parentNode;
    this.parentNodeIndex = parentNodeIndex;
  }
  setCurrentProcess(process, pos) {
    this.currentProcess = process;
    // pos
    this.pos = pos;
  }
}

//   type: 1
class OriginalNode {
  constructor(
    headerBg,
    title,
    titleColor = "#fff",
    { nodeRender, removeNodeMethod }
  ) {
    this.width = RoutineNodeSize.width;
    this.headerHeight = RoutineNodeSize.headerHeight;
    this.headerBg = headerBg;
    this.title = title;
    this.type = 1;
    this.titleColor = titleColor;
    this.nodeRender = nodeRender;
    this.reference = [];
    this.pos = 0;
    this.parentNode = null;
    this.parentNodeIndex = -1;
    this.removeNode = removeNodeMethod;
  }
  setReference(reference, pos) {
    this.reference = reference;
    this.pos = pos;
  }
  setParentNode(parentNode, parentNodeIndex) {
    this.parentNode = parentNode;
    this.parentNodeIndex = parentNodeIndex;
  }
}

// nodeType 1 
class InitiatorNode extends OriginalNode {
  constructor({ nodeRender, removeNodeMethod }) {
    super("#46608C", "", "#fff", { nodeRender, removeNodeMethod });
    this.nodeType = 1;
  }
}

// nodeType 2 
class ApproverNode extends OriginalNode {
  constructor({ nodeRender, removeNodeMethod }) {
    super("#F0A818", "", "#fff", { nodeRender, removeNodeMethod });
    this.nodeType = 2;
  }
}

// nodeType 3 
class CcNode extends OriginalNode {
  constructor({ nodeRender, removeNodeMethod }) {
    super("#2D70F8", "", "#fff", { nodeRender, removeNodeMethod });
    this.nodeType = 3;
  }
}

// nodeType 4 
class AgentNode extends OriginalNode {
  constructor({ nodeRender, removeNodeMethod }) {
    super("#E01738", "", "#fff", { nodeRender, removeNodeMethod });
    this.nodeType = 4;
  }
}

// nodeType 5 
class ConditionNode extends OriginalNode {
  constructor({ nodeRender, removeNodeMethod }) {
    super("#fff", ``, "#76CC20", { nodeRender, removeNodeMethod });
    this.nodeType = 5;
  }
}

export {
  ProcessNode,
  AddConditionNode,
  InitiatorNode,
  ApproverNode,
  CcNode,
  AgentNode,
  ConditionNode,
};
